'use client'

import { useSession, signIn } from 'next-auth/react'
import { useEffect, useState } from 'react'
import Image from 'next/image'
import { Input } from '@/components/ui/input'
import { Button } from '@/components/ui/button'
import { UpdateUser, getUser } from '@/services/recipe.service'

export default function MyProfile() {
  const { data: session, status } = useSession()
  const [nickName, setNickName] = useState('')
  const [imageUrl, setImageUrl] = useState('')
  const [name, setName] = useState('')

  useEffect(() => {
    if (status === 'unauthenticated') signIn()
  }, [status])

  useEffect(() => {
    const load = async () => {
      try {
        const me = await getUser()
        setNickName(me.nickName || '')
        setImageUrl(me.imageUrl || '')
        setName(`${me.firstName ?? ''} ${me.lastName ?? ''}`.trim())
      } catch {}
    }
    load()
  }, [])

  const onSave = async () => {
    await UpdateUser({ nickName, imageUrl })
  }

  return (
    <div className="max-w-xl mx-auto py-8 space-y-6">
      <h1 className="text-2xl font-bold">My Profile</h1>
      <div className="flex items-center gap-4">
        <Image src={imageUrl || '/icons/person.svg'} alt="avatar" width={64} height={64} />
        <div>
          <div className="text-sm text-gray-500">Name</div>
          <div>{name || '-'}</div>
        </div>
      </div>
      <div className="space-y-2">
        <label className="text-sm">Avatar URL</label>
        <Input value={imageUrl} onChange={(e) => setImageUrl(e.target.value)} placeholder="https://..." />
      </div>
      <div className="space-y-2">
        <label className="text-sm">Nickname</label>
        <Input value={nickName} onChange={(e) => setNickName(e.target.value)} placeholder="Your nickname" />
      </div>
      <Button onClick={onSave}>Save</Button>
    </div>
  )
}
 
